import React, { Component } from "react";
import { BiSort } from "react-icons/bi";
import { Link } from "react-router-dom";
import {
  Auth,
  Basic,
  DistrictTotalsSummarySubmitted,
  FC_GetSubmittedPlacementSchools,
} from "../../actions";
import Button from "../../components/Buttons/Button";
import ExportToExcel from "../../components/GenerateReport/ExportToExcel";
import Loading from "../../components/Loading/Loading";
import { DistrictTypes } from "../../types/TransferTypes";
import { commaFy, search } from "../../utils/functions";
import DistrictPlacementDetails from "./DistrictPlacementDetails";
import { GeneralPlacementReportInterface } from "./GeneralPlacementReport";

interface GeneralReportProps {
  loading: boolean;
  generalReport: GeneralPlacementReportInterface[] | null;
  selected_month: string;
  error: string;
  districts: DistrictTypes[] | null;
  basic: Basic;
  selected_district?: {
    district_code: string;
    district_name: string;
  };
  auth: Auth;
}
interface GeneralReportState {
  loading: boolean;
  sortBy: {
    column: "teachers" | "schools_submitted" | "remaining";
    status: "ASC" | "DESC";
  } | null;
  search_district: string;
  selectedDistrictDetails: {
    district_code: string;
    district_name: string;
    details: {
      qualification_name: string;
      total: number;
    }[];
    total: number;
  } | null;
  schools_submitted_placements: DistrictTotalsSummarySubmitted | null;
  pinned_districts: string[];
}

export class GeneralReport extends Component<
  GeneralReportProps,
  GeneralReportState
> {
  constructor(props: GeneralReportProps) {
    super(props);

    this.state = {
      loading: false,
      sortBy: null,
      search_district: "",
      selectedDistrictDetails: null,
      schools_submitted_placements: null,
      pinned_districts:
        (this.props.auth.role?.role_id === "3" ||
          this.props.auth.role?.role_id === "2" ||
          this.props.auth.role?.role_id === "1") &&
        this.props.auth.district !== null
          ? [this.props.auth.district.district_code]
          : [],
    };
  }

  getDistrictSchools = (
    district_code: string,
    schools_submitted_placements: DistrictTotalsSummarySubmitted
  ): {
    total_schools: number;
    placed_schools: number;
  } => {
    const getPlaced =
      schools_submitted_placements.pla_district_total_summary.find(
        (itm) => itm.district_code === district_code
      );
    const getSchools = schools_submitted_placements.district_total_summary.find(
      (itm) => itm.district_code === district_code
    );
    return {
      total_schools: getSchools === undefined ? 0 : parseInt(getSchools.total),
      placed_schools: getPlaced === undefined ? 0 : parseInt(getPlaced.total),
    };
  };
  getDistrictData = (
    district_code: string,
    qualification_name: string
  ): number => {
    const response = this.props.generalReport?.find(
      (itm) =>
        itm.district_code === district_code &&
        itm.qualification.toLocaleLowerCase() ===
          qualification_name.toLocaleLowerCase()
    );
    if (response !== undefined) {
      return parseInt(response.total);
    }
    return 0;
  };
  totalTeachersOnDistrict = (district_code: string) => {
    let total = 0;
    for (const item of this.props.basic.qualifications) {
      total += this.getDistrictData(district_code, item.qualification_name);
    }
    return total;
  };

  sortData = (
    data: {
      district_code: string;
      district_name: string;
      total: number;
      schools: number;
      placed_schools: number;
    }[]
  ) => {
    let result: {
      district_code: string;
      district_name: string;
      total: number;
      schools: number;
      placed_schools: number;
    }[] = [];
    switch (this.state.sortBy?.column) {
      case "teachers":
        result =
          this.state.sortBy?.status === "ASC"
            ? data.sort(function (a, b) {
                return a.total - b.total;
              })
            : this.state.sortBy?.status === "DESC"
            ? data.sort(function (a, b) {
                return b.total - a.total;
              })
            : data;
        break;
      case "schools_submitted":
        result =
          this.state.sortBy?.status === "ASC"
            ? data.sort(function (a, b) {
                return a.placed_schools - b.placed_schools;
              })
            : this.state.sortBy?.status === "DESC"
            ? data.sort(function (a, b) {
                return b.placed_schools - a.placed_schools;
              })
            : data;
        break;
      case "remaining":
        result =
          this.state.sortBy?.status === "ASC"
            ? data.sort(function (a, b) {
                const remainingA = a.schools - a.placed_schools;
                const remainingB = b.schools - b.placed_schools;
                return remainingA - remainingB;
              })
            : this.state.sortBy?.status === "DESC"
            ? data.sort(function (a, b) {
                const remainingA = a.schools - a.placed_schools;
                const remainingB = b.schools - b.placed_schools;
                return remainingB - remainingA;
              })
            : data;
        break;

      default:
        result = data;
        break;
    }
    return result;
  };

  districtsWithTotals = (): {
    district_code: string;
    district_name: string;
    total: number;
    schools: number;
    placed_schools: number;
  }[] => {
    let response: {
      district_code: string;
      district_name: string;
      total: number;
      schools: number;
      placed_schools: number;
    }[] = [];
    if (
      this.props.districts !== null &&
      this.props.generalReport !== null &&
      this.state.schools_submitted_placements !== null
    ) {
      for (const item of this.props.districts) {
        response.push({
          district_code: item.district_code,
          district_name: item.district_name,
          total: this.totalTeachersOnDistrict(item.district_code),
          schools: this.getDistrictSchools(
            item.district_code,
            this.state.schools_submitted_placements
          ).total_schools,
          placed_schools: this.getDistrictSchools(
            item.district_code,
            this.state.schools_submitted_placements
          ).placed_schools,
        });
      }
    }
    response =
      this.state.pinned_districts.length > 0
        ? response.filter(
            (itm) =>
              this.state.pinned_districts.find(
                (test) => test === itm.district_code
              ) !== undefined
          )
        : response;
    return search(this.sortData(response), this.state.search_district) as {
      district_code: string;
      district_name: string;
      total: number;
      schools: number;
      placed_schools: number;
    }[];
  };

  getGrandTotal = () => {
    let total_placed_schools = 0;
    let total_schools = 0;
    let total_pending_schools = 0;
    let total_teachers = 0;
    for (const item of this.districtsWithTotals()) {
      total_placed_schools += item.placed_schools;
      total_schools += item.schools;
      total_pending_schools += item.schools - item.placed_schools;
      total_teachers += item.total;
    }
    return {
      total_placed_schools: total_placed_schools,
      total_schools: total_schools,
      total_pending_schools: total_pending_schools,
      total_teachers: total_teachers,
    };
  };
  componentDidMount(): void {
    if (this.props.selected_district !== undefined) {
      this.setState({
        selectedDistrictDetails: {
          district_code: this.props.selected_district.district_code,
          district_name: this.props.selected_district.district_name,
          details: this.props.basic.qualifications.map((itm) => ({
            qualification_name: itm.qualification_name,
            total: this.getDistrictData(
              this.props.selected_district!.district_code,
              itm.qualification_name
            ),
          })),
          total: this.totalTeachersOnDistrict(
            this.props.selected_district.district_code
          ),
        },
      });
    }
    if (this.state.schools_submitted_placements === null) {
      this.props.basic.academic_calender !== null &&
        FC_GetSubmittedPlacementSchools(
          this.props.basic.academic_calender.academic_year_id,
          this.props.selected_month,
          (
            loading: boolean,
            msg: string,
            res: DistrictTotalsSummarySubmitted | null
          ) => {
            console.log(res);
            this.setState({ loading: loading });
            if (res !== null && loading === false) {
              this.setState({
                schools_submitted_placements: res,
                loading: false,
              });
            }
          }
        );
    }
  }
  pinDistrict = (district_code: string) => {
    if (
      this.props.auth.role?.role_id !== "3" &&
      this.props.auth.role?.role_id !== "2" &&
      this.props.auth.role?.role_id !== "1"
    ) {
      if (
        this.state.pinned_districts.find((itm) => itm === district_code) ===
        undefined
      ) {
        this.setState({
          pinned_districts: [...this.state.pinned_districts, district_code],
        });
      } else {
        this.setState({
          pinned_districts: this.state.pinned_districts.filter(
            (itm) => itm !== district_code
          ),
        });
      }
    }
  };
  render() {
    if (
      this.props.loading === true ||
      this.state.loading === true ||
      this.props.districts === null ||
      this.props.generalReport === null
    ) {
      return (
        <div className="bg-white p-4 flex flex-col items-center justify-center">
          <Loading />
          <div className="animate__animated animate__falseIn animate__infinite mt-2 text-lg font-light">
            Loading, please wait...
          </div>
        </div>
      );
    }
    if (this.state.selectedDistrictDetails !== null) {
      return (
        <DistrictPlacementDetails
          selectedDistrictDetails={this.state.selectedDistrictDetails}
        />
      );
    }
    return (
      <div className="animate__animated animate__fadeIn">
        <div className="flex flex-row justify-between gap-3 mb-5">
          <div className="flex flex-col">
            <span className="font-extrabold text-2xl text-primary-800">
              Teacher placement summary
            </span>
            <span className="-mt-1">
              This file presents number of existing in the district
            </span>
            {/* <Link
              title="Click here to print placement reports"
              to="/placement-books"
              className="underline text-accent-900 font-bold -mt-1"
            >
              Print placement reports
            </Link> */}
          </div>
          <div className="flex flex-row items-center gap-8 justify-end">
            <div className="flex flex-col text-green-700">
              <span>Existing teachers</span>
              <span className="text-3xl font-extrabold -mt-1 text-green-600">
                {commaFy(this.getGrandTotal().total_teachers)}
              </span>
            </div>
            <div className="flex flex-col">
              <span>Total Schools</span>
              <span className="text-3xl font-extrabold -mt-1 text-gray-800">
                {commaFy(this.getGrandTotal().total_schools)}
              </span>
            </div>
            <div className="flex flex-col">
              <span>Submitted Schools</span>
              <span className="text-3xl font-extrabold -mt-1 text-primary-800">
                {commaFy(this.getGrandTotal().total_placed_schools)}
              </span>
            </div>
            <div className="flex flex-col text-yellow-700">
              <span>Pending Schools</span>
              <span className="text-3xl font-extrabold -mt-1 text-accent-900">
                {commaFy(this.getGrandTotal().total_pending_schools)}
              </span>
            </div>
          </div>
        </div>
        <div className="mb-3 flex flex-row items-center gap-2">
          <input
            type="text"
            value={this.state.search_district}
            onChange={(e) => this.setState({ search_district: e.target.value })}
            placeholder="Search by district"
            className="px-3 py-2 rounded border border-gray-400 w-full font-bold text-primary-800"
          />
          <div className="w-56">
            <ExportToExcel
              fileData={this.districtsWithTotals().map((item, i) => ({
                no: `${i + 1}`,
                DistrictCode: `${item.district_code}`,
                DistrictName: `${item.district_name}`,
                A2: `${this.getDistrictData(item.district_code, "a2")}`,
                A1: `${this.getDistrictData(item.district_code, "a1")}`,
                A0: `${this.getDistrictData(item.district_code, "a0")}`,
                TotalPlacedTeachers: `${item.total}`,
                TotalDistrictSchools: `${item.schools}`,
                TotalSchoolsSubmitted: `${item.placed_schools}`,
                PendingSchools: `${item.schools - item.placed_schools}`,
              }))}
              fileName={`Districts Teacher placement summary`}
              btnName="Download EXCEL"
            />
          </div>
        </div>
        <table className="w-full">
          <thead>
            <tr className="text-primary-800 text-left font-extrabold bg-primary-100 text-sm">
              <th className="px-4 py-2 border border-blue-300">#</th>
              <th className="px-4 py-2 border border-blue-300">
                District code
              </th>
              <th className="px-4 py-2 border border-blue-300">
                District name
              </th>
              {this.props.basic.qualifications.map((item, i) => (
                <th
                  className="border border-blue-300 text-center px-4 py-2"
                  key={i + 1}
                >
                  {item.qualification_name}
                </th>
              ))}
              <th className="border border-blue-300 text-left text-green-700 bg-green-50">
                <div
                  onClick={() =>
                    this.setState({
                      sortBy: {
                        status:
                          this.state.sortBy === null
                            ? "DESC"
                            : this.state.sortBy.status === "ASC"
                            ? "DESC"
                            : "ASC",
                        column: "teachers",
                      },
                    })
                  }
                  className=" flex flex-row items-center gap-3 px-4 py-2 hover:bg-primary-100 cursor-pointer text-left"
                >
                  <span>Existing teachers</span>
                  <BiSort />
                </div>
              </th>
              <th className="border border-blue-300 text-center px-3 py-2">
                Total schools
              </th>
              <th className="border border-blue-300 text-center">
                <div
                  onClick={() =>
                    this.setState({
                      sortBy: {
                        status:
                          this.state.sortBy === null
                            ? "DESC"
                            : this.state.sortBy.status === "ASC"
                            ? "DESC"
                            : "ASC",
                        column: "schools_submitted",
                      },
                    })
                  }
                  className=" flex flex-row items-center gap-3 px-4 py-2 hover:bg-primary-100 cursor-pointer text-center"
                >
                  <span>Schools submitted</span>
                  <BiSort />
                </div>
              </th>
              <th className="border border-blue-300 text-center">
                <div
                  onClick={() =>
                    this.setState({
                      sortBy: {
                        status:
                          this.state.sortBy === null
                            ? "DESC"
                            : this.state.sortBy.status === "ASC"
                            ? "DESC"
                            : "ASC",
                        column: "remaining",
                      },
                    })
                  }
                  className=" flex flex-row items-center gap-3 px-4 py-2 hover:bg-primary-100 cursor-pointer text-center"
                >
                  <span>Remaining</span>
                  <BiSort />
                </div>
              </th>
            </tr>
          </thead>
          <tbody>
            {this.districtsWithTotals().length === 0 ? (
              <tr>
                <td
                  colSpan={10}
                  className={`text-center text-2xl font-extralight p-5 animate__animated animate__fadeIn border border-gray-300`}
                >
                  No Result Found!
                </td>
              </tr>
            ) : (
              this.districtsWithTotals().map((item, i) => (
                <tr
                  onClick={() => this.pinDistrict(item.district_code)}
                  key={i + 1}
                  className="hover:bg-primary-100 cursor-pointer"
                >
                  <td className="px-4 py-2 border border-gray-300">{i + 1}</td>
                  <td className="px-4 py-2 border border-gray-300">
                    {item.district_code}
                  </td>
                  <td className="px-4 py-2 border border-gray-300">
                    {item.district_name}
                  </td>
                  {this.props.basic.qualifications.map((itm, k) => (
                    <td
                      key={k + 1}
                      className="px-4 py-2 border border-gray-300 text-center"
                    >
                      {commaFy(
                        this.getDistrictData(
                          item.district_code,
                          itm.qualification_name
                        )
                      )}
                    </td>
                  ))}
                  <td className="px-4 py-2 border border-gray-300 text-green-600 text-center">
                    {commaFy(item.total)}
                  </td>
                  <td className="px-4 py-2 border border-gray-300 text-gray-800 text-center">
                    {this.state.schools_submitted_placements === null
                      ? "Loading..."
                      : commaFy(item.schools)}
                  </td>
                  <td className="px-4 py-2 border border-gray-300 text-blue-600 text-center">
                    {this.state.schools_submitted_placements === null
                      ? "Loading..."
                      : commaFy(item.placed_schools)}
                  </td>
                  <td className="px-4 py-2 border border-gray-300 text-accent-900 text-center">
                    {this.state.schools_submitted_placements === null
                      ? "Loading..."
                      : commaFy(item.schools - item.placed_schools)}
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
        {this.state.pinned_districts.length > 0 &&
          this.props.auth.role?.role_id !== "3" &&
          this.props.auth.role?.role_id !== "2" &&
          this.props.auth.role?.role_id !== "1" && (
            <div className="my-5">
              <Button
                onClick={() => this.setState({ pinned_districts: [] })}
                theme="accent"
              >
                Unpin selected district
              </Button>
            </div>
          )}
        {this.props.auth.role?.role_id === "3" && (
          <div>
            <div className="my-3">
              You can view details for the above summary by viewing schools
              lists and generating PDF format placement reports
            </div>
            <div className="flex flex-row items-center gap-3">
              <Link
                className="bg-accent-800 font-bold px-3 py-2 rounded hover:bg-yellow-800 hover:text-white"
                to={"/placement-list-dde"}
              >
                View placement list details
              </Link>
              <Link
                className="border border-accent-900 text-accent-900 font-bold px-3 py-2 rounded hover:bg-accent-800 hover:text-black"
                to={"/placement-books"}
              >
                Print placement reports
              </Link>
            </div>
          </div>
        )}
      </div>
    );
  }
}
