import React, { Component } from "react";
import { Link } from "react-router-dom";
import { Auth, Basic, ToBePlacedDistrictsPostsInterface } from "../../actions";
import Button from "../../components/Buttons/Button";
import ExportToExcel from "../../components/GenerateReport/ExportToExcel";
import Loading from "../../components/Loading/Loading";
import { DistrictTypes } from "../../types/TransferTypes";
import { commaFy, search } from "../../utils/functions";
import { GeneralPlacementReportInterface } from "./GeneralPlacementReport";

interface DistrictTotalsDistrictsCountsItem {
  Existing: number;
  New: number;
  Total: number;
}

export interface DistrictTotalsDistrictsCounts {
  district: string;
  A2: DistrictTotalsDistrictsCountsItem;
  A1: DistrictTotalsDistrictsCountsItem;
  A0: DistrictTotalsDistrictsCountsItem;
  Total: DistrictTotalsDistrictsCountsItem;
}

interface VacantSummaryProps {
  auth: Auth;
  basic: Basic;
  districts: DistrictTypes[] | null;
  districts_limits: DistrictTotalsDistrictsCounts[];
  districts_placements: GeneralPlacementReportInterface[];
  toBePlacedData: ToBePlacedDistrictsPostsInterface[] | null;
  loading: boolean;
  loadToBePlacedData: () => void;
}
interface VacantSummaryState {
  loading: boolean;
  search_district: string;
  sortBy: {
    status: string;
    column: string;
  } | null;
  pinned_districts: string[];
}

export class VacantSummary extends Component<
  VacantSummaryProps,
  VacantSummaryState
> {
  constructor(props: VacantSummaryProps) {
    super(props);

    this.state = {
      loading: false,
      search_district: "",
      sortBy: null,
      pinned_districts:
        (this.props.auth.role?.role_id === "3" ||
          this.props.auth.role?.role_id === "2" ||
          this.props.auth.role?.role_id === "1") &&
        this.props.auth.district !== null
          ? [this.props.auth.district.district_code]
          : [],
    };
  }
  getDistrictSummaryLimit = (district_name: string) => {
    const test = this.props.districts_limits.find(
      (itm) => itm.district === district_name
    );
    return test === undefined ? null : test;
  };
  getTotalPlacedDistrict = (district_code: string) => {
    let response = 0;
    const total = this.props.districts_placements.filter(
      (itm) => itm.district_code === district_code
    );
    for (const item of total) {
      response += parseInt(item.total);
    }
    return response;
  };
  districtsWithTotals = () => {
    let response =
      this.props.districts === null
        ? []
        : this.props.districts.map((item) => ({
            district_code: item.district_code,
            district_name: item.district_name,
            totals: this.getDistrictSummaryLimit(item.district_name),
            total_placed: this.getTotalPlacedDistrict(item.district_code),
          }));
    response =
      this.state.pinned_districts.length > 0
        ? response.filter(
            (itm) =>
              this.state.pinned_districts.find(
                (test) => test === itm.district_code
              ) !== undefined
          )
        : response;
    return search(response, this.state.search_district) as {
      district_code: string;
      district_name: string;
      totals: DistrictTotalsDistrictsCounts | null;
      total_placed: number;
    }[];
  };

  getDistrictData = (
    district_code: string,
    qualification_name: string
  ): number => {
    const response = this.props.districts_placements.find(
      (itm) =>
        itm.district_code === district_code &&
        itm.qualification.toLocaleLowerCase() ===
          qualification_name.toLocaleLowerCase()
    );
    if (response !== undefined) {
      return parseInt(response.total);
    }
    return 0;
  };
  getDistrictDataAvailable = (
    district_name: string,
    qualification_name: string
  ): number => {
    var res = 0;
    const response = this.props.districts_limits.find(
      (itm) => itm.district === district_name
    );
    if (response !== undefined) {
      switch (qualification_name.toLocaleLowerCase()) {
        case "a2":
          res = response.A2.Total;
          break;
        case "a1":
          res = response.A1.Total;
          break;
        case "a0":
          res = response.A0.Total;
          break;
      }
    }
    return res;
  };
  totalTeachersOnDistrict = (district_code: string) => {
    let total = 0;
    for (const item of this.props.basic.qualifications) {
      total += this.getDistrictData(district_code, item.qualification_name);
    }
    return total;
  };
  totalAvailable = (qualification_name: string) => {
    let total = 0;
    if (this.props.districts !== null) {
      for (const item of this.props.districts) {
        total += this.getDistrictDataAvailable(
          item.district_name,
          qualification_name
        );
      }
    }
    return total;
  };
  totalPlaced = (qualification_id: string) => {
    let total = 0;
    if (this.props.districts !== null) {
      for (const item of this.props.districts) {
        total += this.getDistrictData(item.district_code, qualification_id);
      }
    }
    return total;
  };
  getAllTotalAvailable = () => {
    let total = 0;
    for (const item of this.props.basic.qualifications) {
      total += this.totalAvailable(item.qualification_name);
    }
    return total;
  };
  getAllTotalExisting = () => {
    let total = 0;
    for (const item of this.props.basic.qualifications) {
      total += this.totalPlaced(item.qualification_name);
    }
    return total;
  };
  pinDistrict = (district_code: string) => {
    if (
      this.props.auth.role?.role_id !== "3" &&
      this.props.auth.role?.role_id !== "2" &&
      this.props.auth.role?.role_id !== "1"
    ) {
      if (
        this.state.pinned_districts.find((itm) => itm === district_code) ===
        undefined
      ) {
        this.setState({
          pinned_districts: [...this.state.pinned_districts, district_code],
        });
      } else {
        this.setState({
          pinned_districts: this.state.pinned_districts.filter(
            (itm) => itm !== district_code
          ),
        });
      }
    }
  };

  getDistrictToBePlaced = (
    district_code: string,
    qualification_name: string
  ): number => {
    var total = 0;
    if (this.props.toBePlacedData !== null) {
      const response = this.props.toBePlacedData.find(
        (itm) =>
          itm.district_code === district_code &&
          itm.qualification_name.toLowerCase() ===
            qualification_name.toLocaleLowerCase()
      );
      if (response !== undefined) {
        return parseInt(response.approved);
      }
    }
    return total;
  };

  getDistrictNameToBePlaced = (
    district_name: string,
    qualification_name: string
  ): number => {
    var total = 0;
    if (this.props.toBePlacedData !== null) {
      const response = this.props.toBePlacedData.find(
        (itm) =>
          itm.district_name.toLocaleLowerCase() ===
            district_name.toLocaleLowerCase() &&
          itm.qualification_name.toLowerCase() ===
            qualification_name.toLocaleLowerCase()
      );
      if (response !== undefined) {
        return parseInt(response.approved);
      }
    }
    return total;
  };

  getTotalDistrictToBePlaced = (district_code: string) => {
    var total = 0;
    if (this.props.toBePlacedData !== null) {
      const data_temp = this.props.toBePlacedData.filter(
        (itm) => itm.district_code === district_code
      );
      for (const item of data_temp) {
        total += parseInt(item.approved);
      }
    }
    return total;
  };

  getTotalQualificationToBePlacedByQualification = (
    qualification_name: string
  ) => {
    var total = 0;
    if (this.props.toBePlacedData !== null) {
      const filtered = this.props.toBePlacedData.filter(
        (itm) =>
          itm.qualification_name.toLocaleLowerCase() ===
          qualification_name.toLocaleLowerCase()
      );
      for (const item of filtered) {
        total += parseInt(item.approved);
      }
    }
    return total;
  };

  getTotalQualificationToBePlacedTotals = () => {
    var total = 0;
    if (this.props.toBePlacedData !== null) {
      for (const item of this.props.toBePlacedData) {
        total += parseInt(item.approved);
      }
    }
    return total;
  };

  getTeachersAvailableAndToBePlaced = (
    district_code: string,
    qualification_name: string
  ) => {
    return (
      this.getDistrictToBePlaced(district_code, qualification_name) +
      this.getDistrictData(district_code, qualification_name)
    );
  };
  componentDidMount = () => {
    if (this.props.toBePlacedData === null) {
      this.setState({ loading: true });
      this.props.loadToBePlacedData();
    }
  };

  render() {
    if (this.state.loading === true || this.props.toBePlacedData === null) {
      return (
        <div className="bg-white p-4 flex flex-col items-center justify-center">
          <Loading />
          <div className="animate__animated animate__falseIn animate__infinite mt-2 text-lg font-light">
            Loading, please wait...
          </div>
        </div>
      );
    }
    return (
      <div className="animate__animated animate__fadeIn">
        <div className="flex flex-row justify-between gap-3 mb-5">
          <div className="flex flex-col pl-4 pt-5">
            <span className="font-extrabold text-2xl text-primary-800">
              Posts management
            </span>
            <span className="-mt-1">
              This includes total ceiling and available teachers comparison
            </span>
            <div className="mt-3">
              <ExportToExcel
                fileData={this.districtsWithTotals().map((item, i) => ({
                  no: `${i + 1}`,
                  District_Code: `${item.district_code}`,
                  District_Name: `${item.district_name}`,
                  AO_Placed: `${this.getDistrictData(
                    item.district_code,
                    "a0"
                  )}`,
                  A0_to_be_placed: `${this.getDistrictToBePlaced(
                    item.district_code,
                    "a0"
                  )}`,
                  A0_grand_total: `${this.getTeachersAvailableAndToBePlaced(
                    item.district_code,
                    "a0"
                  )}`,
                  A0_total_ceiling: `${this.getDistrictDataAvailable(
                    item.district_name,
                    "a0"
                  )}`,
                  A1_Placed: `${this.getDistrictData(
                    item.district_code,
                    "a1"
                  )}`,
                  A1_to_be_placed: `${this.getDistrictToBePlaced(
                    item.district_code,
                    "a1"
                  )}`,
                  A1_grand_total: `${this.getTeachersAvailableAndToBePlaced(
                    item.district_code,
                    "a1"
                  )}`,
                  A1_total_ceiling: `${this.getDistrictDataAvailable(
                    item.district_name,
                    "a1"
                  )}`,
                  A2_Placed: `${this.getDistrictData(
                    item.district_code,
                    "a2"
                  )}`,
                  A2_to_be_placed: `${this.getDistrictToBePlaced(
                    item.district_code,
                    "a2"
                  )}`,
                  A2_grand_total: `${this.getTeachersAvailableAndToBePlaced(
                    item.district_code,
                    "a2"
                  )}`,
                  A2_total_ceiling: `${this.getDistrictDataAvailable(
                    item.district_name,
                    "a2"
                  )}`,
                  Grand_total_teachers: `${
                    item.total_placed +
                    this.getTotalDistrictToBePlaced(item.district_code)
                  }`,
                  Grand_total_ceiling: `${
                    item.totals === null ? "0" : item.totals.Total.Total
                  }`,
                  Total_vacant: `${
                    item.totals === null
                      ? "0"
                      : item.totals.Total.Total -
                        (item.total_placed +
                          this.getTotalDistrictToBePlaced(item.district_code))
                  }`,
                }))}
                fileName={`Districts ceiling tracking report`}
                btnName="Download EXCEL"
              />
            </div>
          </div>
          {this.props.auth.role?.role_id !== "3" &&
            this.props.auth.role?.role_id !== "2" &&
            this.props.auth.role?.role_id !== "1" && (
              <div className="">
                <table className="">
                  <tbody>
                    <tr className="bg-primary-100 font-light">
                      <td className="border border-blue-300 px-3">
                        Report type
                      </td>
                      {this.props.basic.qualifications.map((item, i) => (
                        <td
                          key={i + 1}
                          className="px-5 py-1 font-light border border-blue-300 text-gray-800"
                        >
                          {item.qualification_name}
                        </td>
                      ))}
                      <td className="border border-blue-300 px-3">Total</td>
                    </tr>
                    <tr className="bg-gray-100 font-bold">
                      <td className="border border-gray-300 px-3">
                        National Ceiling
                      </td>
                      {this.props.basic.qualifications.map((item, i) => (
                        <td
                          key={i + 1}
                          className="px-5 py-1 font-light border border-gray-300"
                        >
                          {commaFy(
                            this.totalAvailable(
                              item.qualification_name.toLocaleLowerCase()
                            )
                          )}
                        </td>
                      ))}
                      <td className="border border-gray-300 px-3">
                        {commaFy(this.getAllTotalAvailable())}
                      </td>
                    </tr>
                    <tr className="text-green-700">
                      <td className="border border-blue-300 px-3">
                        Current teachers
                      </td>
                      {this.props.basic.qualifications.map((item, i) => (
                        <td
                          key={i + 1}
                          className="px-5 py-1 font-light border border-blue-300"
                        >
                          {commaFy(this.totalPlaced(item.qualification_name))}
                        </td>
                      ))}
                      <td className="border border-blue-300 px-3">
                        {commaFy(this.getAllTotalExisting())}
                      </td>
                    </tr>
                    <tr className="text-yellow-700">
                      <td className="border border-blue-300 px-3">
                        Posts to be placed
                      </td>
                      {this.props.basic.qualifications.map((item, i) => (
                        <td
                          key={i + 1}
                          className="px-5 py-1 font-light border border-blue-300"
                        >
                          {commaFy(
                            this.getTotalQualificationToBePlacedByQualification(
                              item.qualification_name
                            )
                          )}
                        </td>
                      ))}
                      <td className="border border-blue-300 px-3">
                        {commaFy(this.getTotalQualificationToBePlacedTotals())}
                      </td>
                    </tr>
                    <tr className={`bg-accent-400 text-black`}>
                      <td className="border border-blue-300 font-extrabold px-3">
                        Total Vacant
                      </td>
                      {this.props.basic.qualifications.map((item, i) => (
                        <td
                          key={i + 1}
                          className="px-5 py-1 font-bold border border-blue-300"
                        >
                          {commaFy(
                            this.totalAvailable(
                              item.qualification_name.toLocaleLowerCase()
                            ) -
                              this.getTotalQualificationToBePlacedByQualification(
                                item.qualification_name
                              ) -
                              this.totalPlaced(item.qualification_name)
                          )}
                        </td>
                      ))}
                      <td className="border border-blue-300 px-3 font-extrabold text-black">
                        {commaFy(
                          this.getAllTotalAvailable() -
                            this.getTotalQualificationToBePlacedTotals() -
                            this.getAllTotalExisting()
                        )}
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            )}
        </div>
        <div className="mb-3 flex flex-row items-center gap-2">
          <input
            type="text"
            value={this.state.search_district}
            onChange={(e) => this.setState({ search_district: e.target.value })}
            placeholder="Search by district"
            className="px-3 py-2 rounded border border-gray-400 w-full font-bold text-primary-800"
          />
        </div>
        <div
          className="overflow-x-auto"
          style={{ width: "calc(100vw - 300px)" }}
        >
          <table className="">
            <thead>
              <tr className="text-primary-800 text-left font-extrabold bg-primary-100 text-xs">
                <th className="px-1 py-2 border border-blue-300">#</th>
                <th className="px-1 py-2 border border-blue-300">
                  District code
                </th>
                <th className="px-1 py-2 border border-blue-300">
                  District name
                </th>
                {this.props.basic.qualifications.map((item, i) => (
                  <th
                    className="border border-blue-300 text-center"
                    key={i + 1}
                  >
                    <div>
                      <div className="my-2">{item.qualification_name}</div>
                      <table className="w-full text-xs">
                        <thead>
                          <tr>
                            <td className="border border-blue-300 px-1 py-2">
                              <div className="w-14">Placed</div>
                            </td>
                            <td className="border border-blue-300 px-1 py-2">
                              <div className="w-14">To be placed</div>
                            </td>
                            <td className="border border-blue-300 px-1 py-2">
                              <div className="w-14">G.Total</div>
                            </td>
                            <td className="border border-blue-300 px-1 py-2">
                              <div className="w-14">Ceiling</div>
                            </td>
                          </tr>
                        </thead>
                      </table>
                    </div>
                  </th>
                ))}
                <th className="border border-blue-300 text-left text-primary-800">
                  <div
                    onClick={() =>
                      this.setState({
                        sortBy: {
                          status:
                            this.state.sortBy === null
                              ? "DESC"
                              : this.state.sortBy.status === "ASC"
                              ? "DESC"
                              : "ASC",
                          column: "teachers",
                        },
                      })
                    }
                    className=" flex flex-row items-center gap-3 px-2 py-2 hover:bg-primary-100 cursor-pointer text-left"
                  >
                    <span>Total teachers</span>
                    {/* <BiSort /> */}
                  </div>
                </th>
                <th className="border border-blue-300 text-center px-3 py-2">
                  Total Ceiling
                </th>
                <th className="border border-blue-300 text-center">
                  <div
                    onClick={() =>
                      this.setState({
                        sortBy: {
                          status:
                            this.state.sortBy === null
                              ? "DESC"
                              : this.state.sortBy.status === "ASC"
                              ? "DESC"
                              : "ASC",
                          column: "schools_submitted",
                        },
                      })
                    }
                    className=" flex flex-row items-center gap-3 px-2 py-2 hover:bg-primary-100 cursor-pointer text-center"
                  >
                    <span>Vacant posts</span>
                    {/* <BiSort /> */}
                  </div>
                </th>
              </tr>
            </thead>
            <tbody>
              {this.districtsWithTotals().length === 0 ? (
                <tr>
                  <td
                    colSpan={10}
                    className={`text-center text-2xl font-extralight p-5 animate__animated animate__fadeIn border border-gray-300`}
                  >
                    No Result Found!
                  </td>
                </tr>
              ) : (
                this.districtsWithTotals().map((item, i) => (
                  <tr
                    onClick={() => {
                      this.pinDistrict(item.district_code);
                    }}
                    key={i + 1}
                    className="hover:bg-primary-100 cursor-pointer text-xs"
                  >
                    <td className="px-1 py-2 border border-gray-300 text-center">
                      {i + 1}
                    </td>
                    <td className="px-1 py-2 border border-gray-300 text-center">
                      {item.district_code}
                    </td>
                    <td className="px-1 py-2 border border-gray-300">
                      {item.district_name}
                    </td>
                    {this.props.basic.qualifications.map((itm, k) => (
                      <td
                        key={k + 1}
                        className="border border-gray-300 text-center"
                      >
                        <div>
                          <table className="w-full text-xs">
                            <tbody>
                              <tr>
                                <td
                                  className={`border border-blue-300 py-2 ${
                                    this.getDistrictDataAvailable(
                                      item.district_name,
                                      itm.qualification_name
                                    ) >
                                    this.getDistrictData(
                                      item.district_code,
                                      itm.qualification_name
                                    )
                                      ? ""
                                      : "bg-red-100"
                                  }`}
                                >
                                  <div className={`w-6 px-1 text-center`}>
                                    {commaFy(
                                      this.getDistrictData(
                                        item.district_code,
                                        itm.qualification_name
                                      )
                                    )}
                                  </div>
                                </td>
                                <td
                                  className={`border border-blue-300 py-2 ${
                                    this.getDistrictDataAvailable(
                                      item.district_name,
                                      itm.qualification_name
                                    ) >
                                    this.getDistrictData(
                                      item.district_code,
                                      itm.qualification_name
                                    )
                                      ? ""
                                      : "bg-red-100"
                                  }`}
                                >
                                  <div
                                    className={`w-6 px-1 text-center ${
                                      this.getDistrictToBePlaced(
                                        item.district_code,
                                        itm.qualification_name
                                      ) > 0
                                        ? "text-accent-900"
                                        : "text-gray-600"
                                    }`}
                                  >
                                    {commaFy(
                                      this.getDistrictToBePlaced(
                                        item.district_code,
                                        itm.qualification_name
                                      )
                                    )}
                                  </div>
                                </td>
                                <td
                                  className={`border py-2 ${
                                    this.getDistrictDataAvailable(
                                      item.district_name,
                                      itm.qualification_name
                                    ) >=
                                    this.getTeachersAvailableAndToBePlaced(
                                      item.district_code,
                                      itm.qualification_name
                                    )
                                      ? "bg-green-50 text-green-700 border-blue-300"
                                      : "bg-red-600 text-white border-red-700"
                                  }`}
                                >
                                  <div className={`w-6 text-center px-1`}>
                                    {commaFy(
                                      this.getTeachersAvailableAndToBePlaced(
                                        item.district_code,
                                        itm.qualification_name
                                      )
                                    )}
                                  </div>
                                </td>
                                <td
                                  className={`border ${
                                    this.getDistrictDataAvailable(
                                      item.district_name,
                                      itm.qualification_name
                                    ) >=
                                    this.getTeachersAvailableAndToBePlaced(
                                      item.district_code,
                                      itm.qualification_name
                                    )
                                      ? "bg-accent-400 border-blue-300"
                                      : "bg-red-700 text-white border-red-600"
                                  } py-2 text-center`}
                                >
                                  <div className="w-6 px-1">
                                    {commaFy(
                                      this.getDistrictDataAvailable(
                                        item.district_name,
                                        itm.qualification_name
                                      )
                                    )}
                                  </div>
                                </td>
                              </tr>
                            </tbody>
                          </table>
                        </div>
                      </td>
                    ))}
                    <td className="px-1 py-2 border border-gray-300 text-primary-600 text-center">
                      {commaFy(
                        item.total_placed +
                          this.getTotalDistrictToBePlaced(item.district_code)
                      )}
                    </td>
                    <td className="px-1 py-2 border border-gray-300 text-gray-800 text-center">
                      {item.totals === null
                        ? "Loading..."
                        : commaFy(item.totals.Total.Total)}
                    </td>
                    <td
                      className={`px-1 py-2 border ${
                        item.totals === null
                          ? ""
                          : item.totals.Total.Total -
                              (item.total_placed +
                                this.getTotalDistrictToBePlaced(
                                  item.district_code
                                )) <
                            0
                          ? "bg-red-600 text-white border-red-700"
                          : "text-blue-600 border-gray-300"
                      }  text-center`}
                    >
                      {item.totals === null
                        ? 0
                        : commaFy(
                            item.totals.Total.Total -
                              (item.total_placed +
                                this.getTotalDistrictToBePlaced(
                                  item.district_code
                                ))
                          )}
                    </td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>
        {this.state.pinned_districts.length > 0 &&
          this.props.auth.role?.role_id !== "3" &&
          this.props.auth.role?.role_id !== "2" &&
          this.props.auth.role?.role_id !== "1" && (
            <div className="my-5">
              <Button
                onClick={() => this.setState({ pinned_districts: [] })}
                theme="accent"
              >
                Unpin selected district
              </Button>
            </div>
          )}
        {this.props.auth.role?.role_id === "3" && (
          <div>
            <div className="my-3">
              You can view details for the above summary by viewing schools
              lists and generating PDF format placement reports
            </div>
            <div className="flex flex-row items-center gap-3">
              <Link
                className="bg-accent-800 font-bold px-3 py-2 rounded hover:bg-yellow-800 hover:text-white"
                to={"/placement-list-dde"}
              >
                View placement list details
              </Link>
              <Link
                className="border border-accent-900 text-accent-900 font-bold px-3 py-2 rounded hover:bg-accent-800 hover:text-black"
                to={"/placement-books"}
              >
                Print placement reports
              </Link>
            </div>
          </div>
        )}
      </div>
    );
  }
}
