import React, { Component } from "react";
import { MdClose } from "react-icons/md";
import { RiSearchLine } from "react-icons/ri";
import { connect } from "react-redux";
import {
  Auth,
  Basic,
  FC_GetPostsToBePlacedSummaries,
  GetDistrictsLimits,
  GetGeneralPlacementReports,
  Placement,
  ToBePlacedDistrictsPostsInterface,
} from "../../actions";
import { FC_GetDistricts } from "../../actions/transfer.action";
import Loading from "../../components/Loading/Loading";
import { StoreState } from "../../reducers";
import { DistrictTypes } from "../../types/TransferTypes";
import { MONTHS_LIST } from "../../utils/functions";
import { GeneralReport } from "./GeneralReport";
import { DistrictTotalsDistrictsCounts, VacantSummary } from "./VacantSummary";

export interface GeneralPlacementReportInterface {
  district_code: string;
  qualification: string;
  total: string;
}

interface GeneralPlacementReportProps {
  auth: Auth;
  basic: Basic;
  placement: Placement;
}
interface GeneralPlacementReportState {
  loading: boolean;
  report_type: "general" | "vacant" | "search";
  generalReport: GeneralPlacementReportInterface[] | null;
  selected_month: string;
  error: string;
  districts: DistrictTypes[] | null;
  districts_limits: DistrictTotalsDistrictsCounts[] | null;
  districts_to_be_placed: ToBePlacedDistrictsPostsInterface[] | null;
}

const TodayDate = new Date();

class _GeneralPlacementReport extends Component<
  GeneralPlacementReportProps,
  GeneralPlacementReportState
> {
  constructor(props: GeneralPlacementReportProps) {
    super(props);

    this.state = {
      loading: false,
      report_type: "general",
      generalReport: null,
      selected_month: MONTHS_LIST[TodayDate.getMonth()],
      error: "",
      districts: null,
      districts_limits: null,
      districts_to_be_placed: null,
    };
  }
  setLoading = (status: boolean) => this.setState({ loading: status });

  FC_LoadGeneralReport = () => {
    this.setLoading(true);
    this.props.basic.academic_calender !== null &&
      GetGeneralPlacementReports(
        this.props.basic.academic_calender.academic_year_id, //Add selection of academic
        this.state.selected_month,
        (
          loading: boolean,
          msg: string,
          res: GeneralPlacementReportInterface[] | null
        ) => {
          this.setLoading(loading);
          if (loading === false && msg === "" && res !== null) {
            this.setState({ generalReport: res, loading: false });
            console.log("Test report", res);
          } else {
            if (msg !== "") {
              this.setState({ error: msg, generalReport: [] });
            }
          }
        }
      );
  };

  loadToBePlaced = () => {
    if (
      this.state.report_type === "vacant" &&
      this.state.districts_to_be_placed === null
    ) {
      this.setLoading(true);
      FC_GetPostsToBePlacedSummaries(
        (
          loading: boolean,
          msg: string,
          res: ToBePlacedDistrictsPostsInterface[] | null
        ) => {
          this.setLoading(loading);
          if (loading === false && res !== null) {
            this.setState({ districts_to_be_placed: res });
          }
        }
      );
    }
  };

  componentDidMount = () => {
    if (this.state.districts_limits === null) {
      this.setLoading(true);
      GetDistrictsLimits(
        (loading: boolean, res: DistrictTotalsDistrictsCounts[] | null) => {
          this.setState({
            loading: loading,
            districts_limits: res,
          });
        }
      );
    }
    if (this.state.districts === null) {
      this.setLoading(true);
      FC_GetDistricts(
        (status: boolean, data: DistrictTypes[] | null, msg: string) => {
          if (data !== null) {
            this.setState({ districts: data, loading: false });
          }
        }
      );
    }
    if (
      this.state.report_type === "general" &&
      this.state.generalReport === null
    ) {
      this.FC_LoadGeneralReport();
    }
    this.loadToBePlaced();
  };
  render() {
    if (this.state.loading === true) {
      return (
        <div className="bg-white p-4 flex flex-col items-center justify-center">
          <Loading />
          <div className="animate__animated animate__falseIn animate__infinite mt-2 text-lg font-light">
            Loading, please wait...
          </div>
        </div>
      );
    }
    return (
      <div>
        <div className="px-3 md:px-6 py-6">
          <div className="flex flex-row items-center justify-between gap-3">
            <div className="text-2xl font-extrabold">
              General placement report
            </div>
            <div className="flex flex-row items-center justify-end gap-1 bg-white p-1 rounded">
              <div
                onClick={() => {
                  this.state.loading === false &&
                    this.setState({ report_type: "general" });
                }}
                className={`px-3 py-2 rounded w-max hover:bg-yellow-50 hover:text-accent-900 ${
                  this.state.report_type === "general"
                    ? "cursor-not-allowed bg-yellow-100 text-yellow-800 font-bold animate__animated animate__fadeIn"
                    : "bg-white cursor-pointer font-bold"
                }`}
              >
                General report
              </div>
              <div
                onClick={() => {
                  this.state.loading === false &&
                    this.setState({ report_type: "vacant" });
                }}
                className={`px-3 py-2 rounded w-max hover:bg-yellow-50 hover:text-accent-900 ${
                  this.state.report_type === "vacant"
                    ? "cursor-not-allowed bg-yellow-100 text-yellow-800 font-bold animate__animated animate__fadeIn"
                    : "bg-white cursor-pointer font-bold"
                }`}
              >
                Posts Management
              </div>
              <div
                onClick={() => {
                  this.state.loading === false &&
                    this.setState({ report_type: "search" });
                }}
                className={`px-3 py-2 rounded w-max hover:bg-yellow-50 hover:text-accent-900 ${
                  this.state.report_type === "search"
                    ? "cursor-not-allowed bg-yellow-100 text-yellow-800 font-bold animate__animated animate__fadeIn"
                    : "bg-white cursor-pointer font-bold"
                } flex flex-row items-center gap-1`}
              >
                <div>
                  <RiSearchLine className="text-2xl" />
                </div>
                <span>Search</span>
              </div>
            </div>
          </div>
          {/* Report here */}
          <div className="bg-white p-3 rounded mt-5">
            {/* Alerts here */}
            {this.state.error !== "" && (
              <div
                title="Click to close alert"
                onClick={() => this.setState({ error: "" })}
                className="rounded-md bg-red-50 border border-red-200 px-3 py-2 text-base font-light w-full cursor-pointer hover:bg-red-100 flex flex-row items-center justify-between gap-3 mb-3 animate__animated animate__shakeX"
              >
                <span>{this.state.error}</span>
                <div>
                  <MdClose className="text-2xl hover:text-red-600" />
                </div>
              </div>
            )}
            <div>
              {this.state.report_type === "general" && (
                <GeneralReport
                  loading={this.state.loading}
                  generalReport={this.state.generalReport}
                  selected_month={this.state.selected_month}
                  error={this.state.error}
                  districts={this.state.districts}
                  basic={this.props.basic}
                  auth={this.props.auth}
                  //   selected_district={{
                  //     district_code: "11",
                  //     district_name: "Nyarugenge",
                  //   }}
                />
              )}
              {this.state.report_type === "vacant" &&
                this.state.districts_limits !== null &&
                this.state.generalReport !== null && (
                  <VacantSummary
                    districts={this.state.districts}
                    basic={this.props.basic}
                    auth={this.props.auth}
                    districts_limits={this.state.districts_limits}
                    districts_placements={this.state.generalReport}
                    toBePlacedData={this.state.districts_to_be_placed}
                    loading={this.state.loading}
                    loadToBePlacedData={() => {
                      this.loadToBePlaced();
                    }}
                  />
                )}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
  placement,
}: StoreState): { auth: Auth; basic: Basic; placement: Placement } => {
  return { auth: auth, basic: basic, placement: placement };
};

export const GeneralPlacementReport = connect(
  mapStateToProps,
  {}
)(_GeneralPlacementReport);
